import FooterBar from "@/components/FooterBar";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";
import BaiduMap from "vue-baidu-map/components/map/Map.vue";
export default {
  name: "join_us",
  data() {
    return {
      tabsIndex: 0, //tab切换的下标
      swiperOption: {
        autoHeight: true
      },
      //   百度地图使用的数据
      center: { lng: 0, lat: 0 },
      zoom: 3
    };
  },
  mounted() {
    // 根据路由地址调整对应的"人才招聘"或者"联系我们"选项卡
    this.swiper.slideTo(this.$route.params.type);
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  methods: {
    //点击tab选项卡，切换内容
    swiperTabs(index) {
      this.tabsIndex = index;
      this.swiper.slideTo(index);
    },
    // 手动滑动切换内容，改变tab选项卡的选中
    changeTabsIndex() {
      this.tabsIndex = this.swiper.activeIndex;
    }
  },
  components: {
    DropdownBar,
    FooterBar,
    swiper,
    swiperSlide,
    BaiduMap
  }
};
